import * as yup from 'yup';

const validationSchema = yup.object().shape({
  theme: yup
    .string('  ')
    .max(200, '  ,  ')
    .required('   '),
  pupil: yup.mixed().typeError('   ').required(' '),
  subject: yup
    .string(' ')
    .max(100, '  ,  ')
    .typeError('   ')
    .required('  '),
  date: yup.string('  ').required('  '),
  time: yup
    .string('  ')
    .matches(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/, '    : "17:00"'),
});

export default validationSchema;
